.container{
  width: 85%;
  margin: 0px auto;
  overflow: hidden;
}
.mxy__30{
  margin-right: 30px;
}
.my__30{
  padding: 0px 30px;
}
.mxy__15{
  margin: 0px 15px;
}

.main__display{
  display: flex;
}
.header{
    padding: 0 12px;
    height: 60px;
    position: -webkit-sticky;
    position: sticky;
    background-color: #fff;
    z-index: 1000;
    top: 0;
}

.menubar{
    margin-right: 15px;
}

input.search__box{
    width: 88%;
    height: 40px;
    padding: 0px 40px 0px 2px; 
    border:1px solid #CDCDCC;
    border-radius: 2px 0 0 2px;
}
.input__box{
    width: 88%;
    padding: 0 10px; 
}
.header__middle__items{
    width: 70%;
    margin-right: 20px;
}
.search__box__btn{
    height: 40px;
    width: 64px;
    top: 0;
    right: 4%;
    border: 1px solid #CDCDCC;
    cursor: pointer;
    padding: 6px;
    background-color: #F9F9F8;
    border-radius: 0px 2px 2px 0px;
}

.header__left__items{
    width: 25%;
}
.mike__container{
    border-radius: 100%;
    background: #F8F8F9;
    padding: 7px;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__right__items{
    width: 25%;
    justify-content: space-evenly;
}

.top__menu__icons{
    height: 40px;
    width: 40px;
    padding: 7px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.email_container>img{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    padding: 5px;
}

.logo{
    display: flex;
    align-items: center;
    justify-content: center;
}
.Sidebar{
    position:-webkit-sticky;
    position:sticky;
    top: 60px;
    display: flex;
    flex:0.05 1;
    background-color: #fff;
    height: 10vh;
    z-index: 100;
    flex-direction: column;
}
.sidebar__items{
    flex-direction: column;
    transition: 0.3s;
    width: 100%;
    padding: 10px;
    margin-left: -5px;
}

.sidebar__items:hover{
    background: #d2d2d2;
}

.sidebar_txt{
    font-size: 0.7rem;
    line-height: 1rem;
}
.Videoes{
    flex:0.95 1;
    width: 93%;
}
.Filter{
    background-color: #fff;
    flex: 0.95 1;
    height: 50px;
    border-top: 1px solid #dedede;
    border-bottom: 1px solid #dedede;
    overflow-x: hidden;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1000;
    top: 60px;
}

.Filter__container{
    padding: 10px 20px;
    align-items: center;
    justify-content: center;
}

.Filter_item{
    border-radius: 30px;
    border: 1px solid #dedede;
    display: flex;
    padding: 2px 10px;
    background-color: #F2F3F3;
    color: #020302;
}

.Filter_navigation_right{
    position: absolute;
    right: 0;
    height: 40px;
    width: 60px;
    align-items: center;
    justify-content: center;
    display: flex;
    background: #ffffffdb;
    margin: 0;
}

.thumb__img{
    width: 100%;
}

.profile__img{
    border-radius: 100%;
    height: 40px;
    width: 40px;
}

.description{
    color: #000000b5;
    padding: 0px 5px;
    display: flex;
    flex-direction: column;
}   
.title{
    font-size: 16px;
    font-weight: 600;
    color: #2a2929;
}
.channelName{
    font-size: 12px;
    margin-top: 10px;
}

.viewsTimeStamp span{
    font-size: 12px;
}
.view::after{
    content: "\2022";
    margin: 0 4px;
}

.timestamp{
    margin-left: -3px;
}

.video__duration{
    bottom: 3.9%;
    right: 1.1%;
    background: rgba(0,0,0,0.8);
    color: #fff;
    border-radius: 2px;
    padding: 1px 3px;
    font-size: 13px;
}
.video__watch{
    
}

.left__bar{
    padding-top: 1rem;
    padding-left: 3.5rem;   
}

.right__bar{
   
    /* padding-right: 3.5rem; */
}

.video__container{
    flex-direction: column;
}

.video__trending{
    font-size: 12px;
    color: #075FD4;
    margin-top: 20px;
}

.video__options{
    margin-top: 20px;
    display: flex;
    padding: 10px 0;
    border-bottom: 1px solid #d2d2d2;
}



.video__single__option{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
}

.video__single__option > svg {
    margin-top: -3px
}

.video__option__left{
    width: 25%;
}

.video__option__right{
    display: flex;
    width: 73%;
    justify-content: space-between;
}

.video__option__left > .video__single__option{
  justify-content: flex-start;
  font-size: 14px;
  color: ##000000b5;
}
